import React, { Component } from 'react';
import { BotImage } from '../Images';
import { Row, Col, Button } from 'react-bootstrap';

class BotContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }
    componentDidMount() {
    }

    render() {
        const { message, onSubmit } = this.props
        const { suggestionChips } = message

        return (
            <div
                className={`group group-bot`}
            >
                <Row className="chatRow">
                    <Col classNameNOTUSING="botImageCol" xs={0}>
                        <BotImage />
                    </Col>
                    <Col className="botImageCol">
                        {!message.text ?
                            <div id="wave">
                                <span className="dot" />
                                <span className="dot" />
                                <span className="dot" />
                            </div>
                            :
                            <p className="groupText">{message.text}</p>}
                    </Col>
                    <div className="suggestionChipsDiv">
                        {suggestionChips.length > 0 && message.text ? suggestionChips.map((chip, index) => {
                            return <Button
                                className="suggestionChipsBtn"
                                variant="outline-light"
                                key={index}
                                onClick={() => onSubmit(chip.description, true)}
                            >
                                {chip.description}
                            </Button>
                        }) : null}
                    </div>
                </Row>
            </div>
        )
    }
}

export default BotContainer;



