import React, { Component } from "react";
import Header from "./components/Header";
import Dialog from "./components/Dialog";
import Input from "./components/Input";
import { BotImage } from './components/Images';
import "./App.css";
import PropTypes from "prop-types";
import axios from "axios";
import { botDataListAll } from './helpers/dummyJson';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      messages: [],
      allSuggestions: [],
      headerClassName: "",
      isVisible: true,
      showDialogue: true, //this is going to be used for showing the dialogue box
      // showMoreSuggestions: false //this is going to be used for showing more when info
    };
    this.fetchResponse = this.fetchResponse.bind(this);
    this.handleUserSubmit = this.handleUserSubmit.bind(this);
    this.handleCollapse = this.handleCollapse.bind(this);
    this.handleInfoToggle = this.handleInfoToggle.bind(this);
    this.showMore = this.showMore.bind(this);
  }
  componentDidMount() {
    const allBotDataSuggestions = botDataListAll();
    const allSuggestionsArr = [];
    allBotDataSuggestions.map((newitem, index) => { //this adds {category, skill} to an array if does not exist
      if (allSuggestionsArr.some(item => item.category === newitem.category)) {
        // if true
      } else {
        allSuggestionsArr.push({ category: newitem.category, skills: [], extended: false, itemsToShow: 3 })
        // if false
      }
      return ""
    })
    allSuggestionsArr.map((skillItem, index) => { //loops through indiviidual categories and sticks corresponding skill to it's array. 
      allBotDataSuggestions.map((suggestionItem, index) => {
        if (suggestionItem.category === skillItem.category)
          skillItem.skills.push(suggestionItem);
        return ""
      })
      return ""
    })

    this.setState({
      messages: [...this.state.messages,
      {
        isBotTyping: false,
        isUser: false,
        text: this.props.data.suggestionChipsTitle,
        suggestionChips: this.props.data.suggestionChips
      }],
      allSuggestions: allSuggestionsArr
    })
  }

  async fetchResponse(text, isUser) {
    const options = {
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Cache-Control": "no-cache"
      }
    };
    const self = this;
    setTimeout(() => {
      axios.get('http://localhost:8090/api/chatbot', { params: { datatype: text } }, options)
        .then(function (response) {
          self.state.messages.pop();
          self.setState({
            isUser: false,
            messages: [...self.state.messages, {
              isUser, text: !response.data.suggestionChipsTitle
                ?
                response.data.messageResponse

                :
                response.data.suggestionChipsTitle,
              suggestionChips: response.data.suggestionChips,
              isBotTyping: false
            }]
            
          })

        })
        .catch(function (error) {
          console.log('inside catch');
          console.log(error);
          this.setState({
            messages: [
              ...this.state.messages,
              {
                isUser: false, text: "Don't have a response", isBotTyping: true, suggestionChips: [],
              }
            ]
          })
        });
    }, 3000);
  }

  handleUserSubmit(text, isUser) {
    this.setState({
      messages: [
        ...this.state.messages,
        { isUser, text, isBotTyping: false, }
      ],
      showDialogue: true
    })
    setTimeout(() => {
      this.setState({
        messages: [
          ...this.state.messages,
          {
            isUser: false, text: null, isBotTyping: true, suggestionChips: [],
          }
        ]
      })
    }, 1000);
    this.fetchResponse(text, false)
  }

  handleCollapse() {
    this.setState({
      isVisible: !this.state.isVisible
    });
  }

  handleInfoToggle() {
    this.setState({
      showDialogue: !this.state.showDialogue
    });
  }

  showMore(index, newValue) {
    //copy the array first
    const updatedArray = [...this.state.allSuggestions];
    if(updatedArray[index].itemsToShow === 3) {

      updatedArray[index].extended = newValue;
      updatedArray[index].itemsToShow = updatedArray[index].skills.length;
      this.setState({
        allSuggestions: updatedArray,
      });
    } else {
      
      updatedArray[index].extended = newValue;
      updatedArray[index].itemsToShow = 3;
      
      this.setState({
        allSuggestions: updatedArray,
      });
    }

  }

  render() {
    if (Object.keys(this.state.messages).length === 0) {
      return "";
    }
    return (
      <div>
        <div className={this.state.isVisible ? "App" : "App hidden"}>
          <div className="chat">
            <Header
              visible={this.handleCollapse}
              handleInfoToggle={this.handleInfoToggle}
              className={this.state.headerClassName}
              showDialogue={this.state.showDialogue}
            />
            <Dialog
              showDialogue={this.state.showDialogue}
              data={this.props.data}
              allSuggestions={this.state.allSuggestions}
              messages={this.state.messages}
              handleUserSubmit={this.handleUserSubmit}
              showMore={this.showMore}
            />
            <Input
              onSubmit={this.handleUserSubmit}
            />
          </div>
          <BotImage
            imageClicked={this.handleCollapse}
            botImageStyle={{ "cursor": "pointer", "width": "75px" }}
          />
        </div>
      </div>
    );
  }
  static propTypes = {
    optionsWelcome: PropTypes.oneOfType([
      PropTypes.shape({
        input: PropTypes.string.isRequired
      }),
      PropTypes.shape({
        message: PropTypes.string.isRequired
      })
    ])
  };
}

export default App;


