import React from "react";


export const UserImage = props => (
  <img
    className="userImage"
    src="http://www.libertyspecialtymarkets.com/wp-content/uploads/2017/01/profile-default.jpg"
    alt="User"
  />
);

export const BotImage = ({ imageClicked, botImageStyle }) => (
  <img
    src={process.env.PUBLIC_URL + '/images/botIcon.svg'}
    alt="Bot"
    onClick={!imageClicked ? null : imageClicked}
    style={!imageClicked ? null : botImageStyle}
  />
);


