import React, { Fragment } from 'react';
import { Button } from 'react-bootstrap';

const AllSuggestionOptions = ({ suggestionOptions, onSubmit, itemsToShow }) => {
    return (
        <div className="suggestedOptionsDiv">
            {suggestionOptions.slice(0, itemsToShow).map(({ skill }, index) => (
                <Fragment key={index}>

                    <Button
                        className="btnSuggestionOptions"
                        onClick={() => onSubmit(skill, true)}
                        style={{ "margin": "5px", "display": "block", "textAlign": "left" }}
                        variant="outline-light"
                        size="sm">
                        {skill}
                    </Button>
                </Fragment>
            ))}
        </div>
    )
}

export default AllSuggestionOptions;



