import React, { Component } from 'react';
import ReactDOM from 'react-dom';
// import { CSSTransition } from 'react-transition-group';
import { Container } from 'react-bootstrap';
import BotContainer from './BotComponent/BotContainer';
import ListAllSuggestions from './BotComponent/AllSuggestions/ListAllSuggestions';
import UserContainer from './UserComponent/UserContainer';

class Dialog extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    componentDidMount() {
        console.log('scroll into view');
        if (!this.props.showDialogue) {
            const end = ReactDOM.findDOMNode(this.scrollSuggestionTarget);
            end.scrollIntoView({ behavior: 'smooth' });
        }
    }
    scrollToBottom() {
        if (this.props.showDialogue) {
            const end = ReactDOM.findDOMNode(this.scrollTarget);
            end.scrollIntoView({ behavior: 'smooth' });
        }
    }

    componentDidUpdate() {
        this.scrollToBottom();
    }

    render() {
        const { messages, showDialogue, allSuggestions, handleUserSubmit, showMore } = this.props;

        return (
            <section className="messages-wrapper">
                <div className="messages">
                    {showDialogue ?
                        (
                            <React.Fragment>
                                <Container>
                                    {messages.map((message, index) => {
                                        return message.isUser ? <UserContainer key={index} message={message} /> : <BotContainer key={index} onSubmit={this.props.handleUserSubmit} message={message} />
                                    })}
                                </Container>
                                <div style={{ float: "left", clear: "both" }}
                                    ref={el => this.scrollTarget = el} />
                            </React.Fragment>
                        )
                        :
                        (
                            <React.Fragment>
                                <div style={{ float: "left", clear: "both" }}
                                    ref={el => this.scrollSuggestionTarget = el} />
                                <ListAllSuggestions
                                    scrollToBottom={this.scrollToBottom()}
                                    showDialogue={showDialogue}
                                    showMore={showMore}
                                    allSuggestions={allSuggestions}
                                    onSubmit={handleUserSubmit}
                                />

                            </React.Fragment>
                        )
                    }
                </div>
            </section>
        );
    }
}
export default Dialog;


