import React, { Component } from 'react';
import { UserImage } from '../Images';
import { Row, Col } from 'react-bootstrap';

class UserContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }
    componentDidMount() {
    }

    render() {
        const { message } = this.props
        return (
            <div
                className={`group group-user`}
            >
                <Row className="chatRow">
                    <Col className="chatRowUser">
                        <p className="pUserMessage">{message.text}</p>
                    </Col>
                    <Col className="userImageCol" xs={0}>
                        <UserImage />
                    </Col>
                </Row>
            </div>
        )
    }
}

export default UserContainer;



