import React, { Fragment } from 'react';
import { Button } from 'react-bootstrap';
import AllSuggestionTitle from './AllSuggestionTitle';
import AllSuggestionOptions from './AllSuggestionOptions';

const ListAllSuggestions = ({ allSuggestions, onSubmit, showMore }) => {
    const refs = allSuggestions.reduce((acc, value, newindex) => {
        acc[newindex] = React.createRef();
        return acc;
    }, {});

    const handleScroll = id =>
        refs[id].current.scrollIntoView({
            behavior: 'smooth',
            block: 'start',
        });

    const handleClick = (id, isUser) => {
        handleScroll(id)
        showMore(id, isUser)
    }

    return (
        <div className="test">
            {allSuggestions.map((suggestion, index) => {

                return (
                    <Fragment key={index}>
                        <AllSuggestionTitle
                            title={suggestion.category}
                            myRef={refs[index]}
                        />
                        <AllSuggestionOptions
                            itemsToShow={suggestion.itemsToShow}
                            suggestionOptions={suggestion.skills}
                            onSubmit={onSubmit}
                        />
                        {suggestion.extended ?
                            (
                                <Button className="showMoreLess" onClick={() => handleClick(index, false)} variant="outline-warning">Show Less</Button>
                            )
                            :
                            (
                                <Button className="showMoreLess" onClick={() => handleClick(index, true)} variant="outline-light">Show more</Button>
                            )
                        }
                    </Fragment>
                )
            })}
        </div>
    )
}

export default ListAllSuggestions;



