import React from "react";
import { Row, Col, Container } from 'react-bootstrap';

const Header = ({ className, visible, handleInfoToggle, showDialogue }) => {
  if (visible) {
  }

  return (
    <header className={className}>
      <Container>
        <Row>
          <Col>
            <button
              className="btn-collapse"
              type="collapse"
              onClick={() => visible("minus")}
            >
              <i className="material-icons">remove</i>
            </button>
          </Col>
          <Col>
            <button
              className="btn-collapse"
              type="collapse"
              onClick={() => handleInfoToggle()}
            >
              <i className="material-icons">{showDialogue ? "info" : "keyboard_arrow_left"}</i> 
              {/* 
                maybe move info icon inside of the chat? √
                showDialogue: false (show backarrow) √
                showDialogue: true (show info icon) √
                add smooth transition between the two
              */}
            </button>
          </Col>
          <Col>
            <button
              className="btn-collapse"
              type="collapse"
              onClick={() => visible("exit")}
            >
              <i className="material-icons">clear</i>
            </button>
          </Col>
        </Row>
      </Container>
    </header>
  );
}
export default Header;



